[data-list] {
  .sort[data-sort] {
    white-space: nowrap;
    cursor: pointer;
    &::after {
      content: url("data:image/svg+xml;base64,");
      margin-left: .25rem;
    }
  }
  .sort[data-sort].asc {
    &::after {
      content: url("data:image/svg+xml;base64,") !important;
      margin-top: -1px !important;
    }
  }
  .sort[data-sort].desc {
    &::after {
      content: url("data:image/svg+xml;base64,") !important;
      margin-top: -1px !important;
    }
  }

  //pagination 
  .page {
    @extend .btn;
    @extend .btn-sm;
    @extend .btn-falcon-default;
    margin-left: map-get($spacers, 1);
    margin-right: map-get($spacers, 1);
  }
  li.active {
    .page{
      background-color: transparent;
      color: var(--#{$prefix}primary) !important;
    }
  }
}

[data-list-pagination].btn-primary[disabled] {
  @extend .btn-light;
  .ios &{
    opacity: 1 !important;
    border: 0 !important;
    background-color: var(--#{$prefix}btn-disabled-custom-background) !important;
    color: var(--#{$prefix}btn-disabled-custom-color) !important;
  }
} 
