/*-----------------------------------------------
|   Theme Styles
-----------------------------------------------*/
/*-----------------------------------------------
|   Reboot
-----------------------------------------------*/
::placeholder {
  opacity: 1;
  color: var(--falcon-gray-400);
}

::selection {
  background-color: var(--falcon-primary);
  color: #fff;
}

[id] {
  scroll-margin-top: calc(var(--falcon-top-nav-height) + 1.5rem);
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  position: relative;
}

pre,
code {
  -webkit-font-smoothing: auto;
  -moz-osx-font-smoothing: auto;
}

section {
  position: relative;
  padding-top: 4rem;
  padding-bottom: 4rem;
}
@media (min-width: 992px) {
  section {
    padding-top: 7.5rem;
    padding-bottom: 7.5rem;
  }
}

input,
button,
select,
optgroup,
textarea,
label,
.alert,
.badge,
.blockquote-footer,
.btn,
.falcon-data-table .paging_full_numbers .page-link,
.falcon-data-table .paging_first_last_numbers .page-link,
.falcon-data-table .paging_full .page-link,
.falcon-data-table .paging_numbers .page-link,
.falcon-data-table .paging_simple_numbers .page-link,
[data-list] .page,
.tox .tox-menu__footer .tox-button:last-child,
.tox .tox-dialog__footer .tox-button:last-child,
.tox .tox-menu__footer .tox-button--secondary,
.tox .tox-dialog__footer .tox-button--secondary,
.navbar,
.pagination,
.valid-feedback,
.invalid-feedback {
  font-family: var(--falcon-font-sans-serif);
}

pre {
  margin: 0;
  transform: translate3d(0, 0, 0);
}

strong {
  font-weight: 600;
}

label {
  font-size: 0.8333333333rem;
  font-weight: 500;
  margin-bottom: 0.5rem;
  letter-spacing: 0.02em;
}

pre,
code,
kbd,
samp {
  font-size: 0.875rem;
}

[dir=rtl] pre, [dir=rtl] code, [dir=rtl] kbd, [dir=rtl] samp {
  direction: rtl;
}

/* -------------------------------------------------------------------------- */
/*                                   Navbar                                   */
/* -------------------------------------------------------------------------- */
.navbar .nav-link {
  outline: none;
}
.navbar .dropdown-menu {
  overflow: visible;
  left: -0.5625rem;
}
.navbar .dropdown-menu.dropdown-caret:after {
  content: "";
  position: absolute;
  z-index: -1;
  width: 1rem;
  height: 1rem;
  background: #fff;
  background: inherit;
  top: -0.375rem;
  left: 1.8rem;
  transform: rotate(45deg);
  border-radius: 0.125rem;
  border-width: 1px 0 0 1px;
  border-style: solid;
  border-color: var(--falcon-border-color);
  box-shadow: var(--falcon-box-shadow-sm);
}
.navbar .dropdown-menu.dropdown-menu-end {
  left: auto;
  right: -0.5625rem;
}
.navbar .dropdown-menu.dropdown-menu-end:after {
  right: 0.95rem;
  left: auto;
}
.navbar .dropdown-item {
  font-weight: 500;
}
.navbar .dropdown-item:hover, .navbar .dropdown-item:focus {
  background-color: var(--falcon-nav-dropdown-link-hover-bg);
  color: var(--falcon-gray-1000);
}

.navbar-toggler-humburger-icon {
  height: 2.5rem;
  width: 2.5rem;
  padding: 0.3125rem;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  box-shadow: none;
}
.navbar-toggler-humburger-icon:hover, .navbar-toggler-humburger-icon:focus {
  background-color: var(--falcon-gray-300) !important;
}

.navbar-toggle-icon {
  position: relative;
  height: 0.125rem;
  width: 1.25rem;
  transition: all 0.2s ease-in-out;
  display: block;
  top: -0.3125rem;
  padding-right: 0;
}
.navbar-toggle-icon .toggle-line {
  display: block;
  height: 100%;
  border-radius: 0.25rem;
  transition: all 0.2s ease-in-out;
  background-color: var(--falcon-gray-700);
}
.navbar-toggle-icon:after, .navbar-toggle-icon:before {
  content: "";
  position: absolute;
  height: 0.125rem;
  background-color: var(--falcon-gray-700);
  left: 0;
  transition: all 0.2s ease-in-out;
  border-radius: 0.25rem;
  width: 100%;
}
.navbar-toggle-icon:before {
  top: 0.3125rem;
}
.navbar-toggle-icon:after {
  top: 0.625rem;
}

.navbar-glass {
  background-color: var(--falcon-bg-navbar-glass);
}

.navbar-brand {
  font-weight: 800;
}

.navbar-glass-shadow {
  box-shadow: 0 0.5rem 0.5rem -0.5rem rgba(0, 0, 0, 0.2) !important;
}

